label {
  &.form-label {
    cursor: pointer;
  }
}

.form-control {
  caret-color: $input-color;
  text-overflow: ellipsis;

  &[disabled] {
    -webkit-text-fill-color: $input-disabled-color;
    cursor: not-allowed;
  }

  @include media-breakpoint-down(lg) {
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: $border-radius-sm;
  }
}

textarea {
  &.form-control {
    height: 200px;
  }
}

.form-select {
  &:not([disabled]) {
    cursor: pointer;
  }

  &[disabled] {
    cursor: not-allowed;
  }
}

.position-relative {
  > .invalid-feedback {
    position: absolute;
    top: calc(100% + $form-label-margin-bottom);
    left: 0;
    z-index: 5;
    margin-top: 0;
  }
}
