.card {
  &-icon-text {
    display: flex;
    width: 100%;
    padding-top: 20px;

    @include media-breakpoint-up(md) {
      padding-top: 30px;
    }

    &.icon-lg {
      padding-top: 30px;

      @include media-breakpoint-up(md) {
        padding-top: 35px;
      }

      .card-icon-text {
        &__icon {
          @include size(60px);

          @include media-breakpoint-up(md) {
            @include size(70px);
          }
        }

        &__icon-holder {
          @include size(28px);

          @include media-breakpoint-up(md) {
            @include size(40px);
          }
        }
      }
    }

    &__content {
      position: relative;
      width: 100%;
      padding: 40px 24px;
      text-align: center;
      background-color: $white;
      border-radius: $border-radius-xl;
      box-shadow: $box-shadow;

      @include media-breakpoint-up(md) {
        padding-top: 60px;
      }
    }

    &__icon {
      @include size(40px);
      @include inline-flex-center;

      position: absolute;
      top: 0;
      left: 50%;
      z-index: 5;
      background-color: $blue;
      border-radius: $border-radius-pill;
      transform: translate(-50%, -50%);

      @include media-breakpoint-up(md) {
        @include size(60px);
      }
    }

    &__icon-holder {
      @include size(24px);

      position: relative;
      overflow: hidden;

      @include media-breakpoint-up(md) {
        @include size(32px);
      }
    }

    &__text-wrap {
      &.with-collapse {
        position: relative;
        padding-bottom: 32px;

        &.clamped {
          .card-icon-text__collapse-toggle {
            visibility: visible;
            opacity: 1;
          }
        }

        &.active {
          .card-icon-text__collapse-toggle-text-hidden {
            display: none;
          }
        }

        &:not(.active) {
          .card-icon-text__text {
            @include truncate-text-to-specific-line(4);

            @include media-breakpoint-up(md) {
              @include truncate-text-to-specific-line(6);

              height: 124px;
            }

            @include media-breakpoint-up(lg) {
              height: 156px;
            }
          }

          .card-icon-text__collapse-toggle-text-open {
            display: none;
          }
        }
      }
    }

    &__collapse-toggle {
      position: absolute;
      bottom: 0;
      left: 50%;
      z-index: 5;
      display: inline-flex;
      color: $blue;
      font-weight: $font-weight-bold;
      font-size: 1rem;
      line-height: 1.1;
      transform: translate(-50%, 0);
      visibility: hidden;
      opacity: 0;
      transition:
        opacity 0.3s,
        visibility 0.3s;

      &:after {
        position: absolute;
        right: 0;
        bottom: 0;
        display: block;
        width: 100%;
        height: 1px;
        background-color: currentColor;
        transition: width 0.5s;
        content: "";
      }

      &:hover {
        &:after {
          width: 0;
        }
      }
    }

    &__btn {
      padding-top: 40px;

      @include media-breakpoint-up(md) {
        padding-top: 48px;
      }
    }
  }

  &-info-list {
    .card-info {
      &:not(:last-child) {
        margin-bottom: 40px;

        @include media-breakpoint-up(md) {
          margin-bottom: 80px;
        }
      }
    }

    &:not(.reversed) {
      .card-info {
        &:nth-child(2n + 1) {
          @include media-breakpoint-up(xxl) {
            background-image: url("../img/card-info/bg-1.svg");
            background-repeat: no-repeat;
            background-position: bottom right 20px;
            background-size: 310px;
          }
        }

        &:nth-child(2n) {
          @extend .card-info, .reversed;

          @include media-breakpoint-up(xxl) {
            background-image: url("../img/card-info/bg-2.svg");
            background-repeat: no-repeat;
            background-position: bottom left 20px;
            background-size: 230px;
          }
        }
      }
    }

    &.reversed {
      .card-info {
        &:nth-child(2n) {
          @include media-breakpoint-up(xxl) {
            background-image: url("../img/card-info/bg-1.svg");
            background-repeat: no-repeat;
            background-position: bottom right 20px;
            background-size: 310px;
          }
        }

        &:nth-child(2n + 1) {
          @extend .card-info, .reversed;

          @include media-breakpoint-up(xxl) {
            background-image: url("../img/card-info/bg-2.svg");
            background-repeat: no-repeat;
            background-position: bottom left 20px;
            background-size: 230px;
          }
        }
      }
    }
  }

  &-info {
    &.reversed {
      .card-info__row {
        @include media-breakpoint-up(md) {
          flex-direction: row-reverse;
        }
      }

      .card-info__img-col {
        @include media-breakpoint-up(xl) {
          margin-left: 8.33333333%;
        }
      }

      .card-info__content-col {
        @include media-breakpoint-up(xl) {
          margin-left: 0;
        }
      }
    }

    &.centered {
      .card-info__row {
        align-items: center;
      }
    }

    &.reduced-img {
      .card-info__img {
        @include responsive-ratio(610, 380);
      }
    }

    &__row {
      @include media-breakpoint-down(md) {
        flex-direction: column-reverse;
      }
    }

    &__content-col {
      @include media-breakpoint-up(xl) {
        margin-left: 8.33333333%;
      }
    }

    &__img {
      @include responsive-ratio(610, 500);

      position: relative;
      overflow: hidden;
      background-color: $blue-faded;
      border-radius: $border-radius-xl;
    }

    &__btn {
      padding-top: 24px;

      @include media-breakpoint-up(md) {
        padding-top: 40px;
      }
    }
  }

  &-process {
    display: flex;
    width: 100%;
    padding-top: 20px;

    @include media-breakpoint-up(md) {
      padding-top: 30px;
    }

    &__content {
      position: relative;
      width: 100%;
      padding: 40px 24px;
      text-align: center;
      background-color: $white;
      border-radius: $border-radius-xl;
      box-shadow: $box-shadow;

      @include media-breakpoint-up(md) {
        padding-top: 50px;
      }
    }

    &__number {
      @include size(40px);
      @include inline-flex-center;

      position: absolute;
      top: 0;
      left: 50%;
      z-index: 5;
      padding-bottom: 8px;
      color: $white;
      font-weight: $font-weight-medium;
      font-size: 36px;
      font-family: $headings-font-family;
      background-color: $blue;
      border-radius: $border-radius-pill;
      transform: translate(-50%, -50%);

      @include media-breakpoint-up(md) {
        @include size(60px);

        padding-bottom: 10px;
        font-size: 48px;
      }
    }
  }
}
