.btn {
  @include inline-flex-center;

  gap: 0.25rem;
  letter-spacing: 0.01em;
  text-transform: uppercase;

  // sizes
  &:not(.btn-sm, .btn-lg) {
    width: 100%;
    min-height: 48px;

    @include media-breakpoint-up(xs) {
      width: auto;
      min-width: 294px;
    }

    @include media-breakpoint-up(lg) {
      min-height: 58px;
    }

    &:hover {
      --btn-hover-box-shadow-gutter: 8px;
    }
  }

  &-sm {
    min-height: 40px;

    [class*="icomoon"] {
      font-size: 1.5em;
    }

    &:hover {
      --btn-hover-box-shadow-gutter: 6px;
    }
  }

  // colors
  &-primary {
    @include button-variant($primary, $primary, $blue-dark, $primary, $primary, $blue-dark, lighten($primary, 10%), lighten($primary, 10%), $blue-dark);

    &:hover {
      --btn-hover-box-shadow-color: #{$blue-dark};

      box-shadow: var(--btn-hover-box-shadow-gutter) var(--btn-hover-box-shadow-gutter) var(--btn-hover-box-shadow-color);
    }
  }

  &-blue-light {
    @include button-variant($blue-light, $blue-light, $blue-dark, $blue-light, $blue-light, $blue-dark, lighten($blue-light, 10%), lighten($blue-light, 10%), $blue-dark);

    &:hover {
      --btn-hover-box-shadow-color: #{$green-dark};

      box-shadow: var(--btn-hover-box-shadow-gutter) var(--btn-hover-box-shadow-gutter) var(--btn-hover-box-shadow-color);
    }
  }

  &.w-100 {
    min-width: auto;
  }
}

.bg-blue-dark,
.bg-gradient {
  .btn {
    &-primary {
      &:hover {
        --btn-hover-box-shadow-color: #{$green-dark};
      }
    }
  }
}
