@keyframes dogs-tail-animation {
  0% {
    transform: rotate(-4deg) translate(-4px, -7px);
  }

  25% {
    transform: rotate(6deg) translate(-16px, 0);
  }

  50% {
    transform: rotate(-4deg) translate(-4px, -7px);
  }

  75% {
    transform: rotate(3deg) translate(-8px, 0);
  }

  100% {
    transform: rotate(-4deg) translate(-4px, -7px);
  }
}
