.header {
  display: flex;
  align-items: center;
  min-height: $header-mobile-height;
  padding: 8px 0;

  @include media-breakpoint-up(lg) {
    min-height: $header-desktop-height;
  }

  &.fixed-top {
    ~ .main {
      padding-top: $header-mobile-height;

      @include media-breakpoint-up(lg) {
        padding-top: $header-desktop-height;
      }
    }
  }

  &__logo {
    display: inline-flex;
    max-width: 140px;

    @include media-breakpoint-up(lg) {
      max-width: 170px;
    }
  }

  &__menu-toggler {
    @include btn-reset;
    @include inline-flex-center;
    @include size(44px);

    position: relative;
    overflow: hidden;
    color: $white;
    font-size: 44px;
    line-height: 1;
    transition: color 0.3s;

    &:hover {
      @include media-breakpoint-up(md) {
        color: $primary;
      }
    }

    &:after {
      position: absolute;
      top: 50%;
      right: 0;
      z-index: 5;
      font-family: "icomoon";
      transform: translate(100%, -50%);
      transition: transform 0.3s;
      content: "\e909";
    }

    [class*="icomoon-"] {
      display: inline-block;
      transition: opacity 0.3s;
    }

    &.active {
      &:after {
        transform: translate(0, -50%);
      }

      [class*="icomoon-"] {
        opacity: 0;
      }
    }
  }
}
