.footer {
  color: $white;

  &__top-content {
    padding: 24px 0;

    @include media-breakpoint-up(lg) {
      padding: 40px 0;
    }
  }

  &__bottom-content {
    padding: 24px 0;
    border-top: 1px solid $blue-light;

    @include media-breakpoint-up(lg) {
      padding: 40px 0;
    }
  }

  &__logo {
    display: inline-flex;
    max-width: 140px;

    @include media-breakpoint-up(lg) {
      max-width: 170px;
    }
  }
}
