.offcanvas {
  &-menu {
    @include media-breakpoint-down(xxs) {
      width: 280px !important;
    }

    &.offcanvas-xl {
      @include media-breakpoint-down(xl) {
        top: $header-desktop-height;
      }

      @include media-breakpoint-down(lg) {
        top: $header-mobile-height;
      }

      + .offcanvas-backdrop {
        @include media-breakpoint-down(xl) {
          top: $header-desktop-height;
        }

        @include media-breakpoint-down(lg) {
          top: $header-mobile-height;
        }
      }
    }

    .offcanvas-body {
      @extend %custom-scrollbar;
    }
  }
}
