.accordion {
  border-top: $accordion-border-width solid $accordion-border-color;
  border-bottom: $accordion-border-width solid $accordion-border-color;

  &-button {
    position: relative;
    padding-right: 2rem;
    font-size: inherit;
    box-shadow: none !important;

    @include media-breakpoint-up(sm) {
      padding-right: calc($accordion-icon-width + 1.5rem);
    }

    &:after {
      position: absolute;
      top: $accordion-padding-y;
      right: 0.75rem;
      z-index: 5;
      height: auto;
      color: transparent;
      background-position: center;
      background-size: 100%;
      content: "H3";

      @include media-breakpoint-down(sm) {
        right: 0;
        width: 16px;
      }
    }

    &:hover {
      color: $accordion-icon-color;
    }
  }

  &-body {
    padding-top: 0;
  }
}
