@if $use-title-palette {
  @include title-sizes;
}

.text-block,
.remove-last-child-margin {
  > * {
    &:last-child {
      margin-bottom: 0 !important;
    }
  }

  &.row {
    > * {
      &.col,
      &[class^="col-"] {
        &:last-child {
          > * {
            margin-bottom: 0 !important;
          }
        }
      }
    }
  }
}

.text-sm {
  font-size: 1rem !important;
  line-height: $line-height-sm !important;

  @include media-breakpoint-up(lg) {
    font-size: $font-size-sm !important;
  }
}

.text-lg {
  font-size: 1.25rem !important;
  line-height: $line-height-lg;

  @include media-breakpoint-up(lg) {
    font-size: $font-size-lg !important;
  }
}

h1,
.h1 {
  margin-bottom: 24px;

  @include media-breakpoint-up(lg) {
    margin-bottom: 40px;
  }
}

h2,
.h2 {
  margin-bottom: 24px;

  @include media-breakpoint-up(md) {
    margin-bottom: 40px;
  }

  @include media-breakpoint-down(xxs) {
    font-size: 26px;
  }
}

h3,
.h3 {
  font-weight: $font-weight-medium;
}

h4,
.h4 {
  font-weight: $font-weight-medium;
  font-family: inherit;
}

h5,
.h5 {
  font-family: inherit;
}

h6,
.h6 {
  font-family: inherit;
}

.display-2 {
  font-weight: $font-weight-medium;
  font-size: 1.5rem;
  font-family: inherit;

  @include media-breakpoint-up(lg) {
    font-size: 1.75rem;
  }

  @include media-breakpoint-up(xl) {
    font-size: map-get($display-font-sizes, 2);
  }
}

.display-4 {
  font-size: 1.375rem;
  font-family: inherit;

  @include media-breakpoint-up(md) {
    font-size: map-get($display-font-sizes, 4);
  }
}

.title-with-check {
  position: relative;
  padding-left: 52px;

  &:before {
    position: absolute;
    top: 50%;
    left: 4px;
    z-index: 5;
    color: $primary;
    font-size: 33px;
    font-family: "icomoon";
    transform: translate(0, -50%);
    content: "\e910";

    @include media-breakpoint-up(md) {
      left: 0;
      font-size: 40px;
    }
  }
}

.title-with-icon {
  position: relative;
  padding-left: 52px;

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;

    &:before {
      color: transparent;
      opacity: 0;
      content: "h2";
      pointer-events: none;
    }
  }

  &__img-holder {
    @include size(40px);

    position: absolute;
    top: 50%;
    left: 0;
    z-index: 5;
    overflow: hidden;
    transform: translate(0, -50%);
  }
}

.notice-text {
  padding: 32px 16px;
  background-color: $blue-faded;
  border-radius: $border-radius-xl;

  @include media-breakpoint-up(sm) {
    padding: 32px;
  }

  &__block {
    position: relative;
    padding-left: 36px;
  }

  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    display: inline-flex;
    align-items: center;
    width: 1lh;
    height: 1lh;
    color: $blue;

    [class*="icomoon"] {
      font-size: 26px;
    }
  }
}
