$section-indent-mobile: 20px;
$section-indent: 40px;

.section {
  padding: $section-indent-mobile 0;

  @include media-breakpoint-up(sm) {
    padding: $section-indent 0;
  }

  &-welcome {
    position: relative;

    @include media-breakpoint-down(sm) {
      padding-top: 250px;
      padding-bottom: $section-indent;
    }

    @include media-breakpoint-up(lg) {
      padding: 130px 0;
    }

    @include media-breakpoint-up(xl) {
      min-height: 760px;
    }

    + .section-line,
    + .section-logos,
    + .section-contacts,
    + .section-reclaim {
      padding-top: 0;
    }

    + .section:not(.section-line, .section-logos, .section-contacts, .section-reclaim) {
      padding-top: $section-indent-mobile * 2;

      @include media-breakpoint-up(sm) {
        padding-top: $section-indent * 2;
      }
    }

    &__img {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 360px;
      pointer-events: none;

      @include media-breakpoint-up(sm) {
        bottom: 0;
        height: 100%;
      }

      &:before {
        @include absolute-cover;

        z-index: 1;
        display: block;
        background: linear-gradient(to top, $light 15%, rgba($light, 0.25) 50%, rgba($light, 0) 67.04%);
        content: "";
        pointer-events: none;

        @include media-breakpoint-up(sm) {
          background: linear-gradient(to right, $light 0%, rgba($light, 0.6) 44.72%, rgba($light, 0) 55.69%);
        }
      }
    }
  }

  &-intro {
    position: relative;
    display: flex;
    align-items: center;
    padding: 250px 0 40px;

    @include media-breakpoint-up(md) {
      padding-top: 40px;
    }

    @include media-breakpoint-up(lg) {
      min-height: 520px;
    }

    + .section-line,
    + .section-logos,
    + .section-contacts,
    + .section-reclaim {
      padding-top: 0;
    }

    + .section:not(.section-line, .section-logos, .section-contacts, .section-reclaim) {
      padding-top: $section-indent-mobile * 2;

      @include media-breakpoint-up(sm) {
        padding-top: $section-indent * 2;
      }
    }

    &__img {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 360px;
      pointer-events: none;

      @include media-breakpoint-up(md) {
        bottom: 0;
        height: 100%;
      }

      &:before {
        @include absolute-cover;

        z-index: 1;
        display: block;
        background: linear-gradient(to top, $light 15%, rgba($light, 0.25) 50%, rgba($light, 0) 67.04%);
        content: "";
        pointer-events: none;

        @include media-breakpoint-up(md) {
          background: linear-gradient(to right, $light 35%, rgba($light, 0.6) 50%, rgba($light, 0) 60%);
        }
      }
    }
  }

  &-achievements {
    &__block {
      position: relative;
      z-index: 10;
      padding: 40px 20px;
      background-color: $blue-faded;
      border-radius: $border-radius-xl;

      @include media-breakpoint-up(lg) {
        padding-right: 40px;
        padding-left: 40px;
      }
    }

    &__img {
      @include responsive-ratio(440, 198);

      position: relative;
      max-width: 250px;
      margin: 0 auto;

      @include media-breakpoint-up(lg) {
        max-width: 440px;
      }

      img {
        z-index: 5;
        object-position: bottom center;
      }

      &:after {
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 1;
        width: 1px;
        height: 1px;
        box-shadow: 0 0 40px 90px rgba($blue, 0.1);
        transform: translate(-50%, 0);
        content: "";

        @include media-breakpoint-up(lg) {
          box-shadow: 0 0 80px 185px rgba($blue, 0.1);
        }
      }
    }
  }

  &-logos {
    &__block {
      padding: 40px 0;
      background-color: $blue-faded;
    }

    &__img {
      @include responsive-ratio(240, 46);

      position: relative;
      max-width: 240px;
      margin: 0 auto;
      overflow: hidden;
    }
  }

  &-faq {
    background-repeat: no-repeat;
    background-position:
      top 0 right 8px,
      bottom 0 left 8px;
    background-size: 140px;

    @include media-breakpoint-down(lg) {
      background-image: none !important;
    }

    @include media-breakpoint-up(xxl) {
      background-position:
        top 0 right 20px,
        bottom 0 left 20px;
      background-size: 200px;
    }
  }

  &-contacts {
    &__block {
      padding: 40px 0;

      @include media-breakpoint-up(md) {
        padding: 60px 0;
      }

      @include media-breakpoint-up(lg) {
        padding: 80px 0;
      }
    }
  }

  &-info-panel {
    &__block {
      padding: 40px 20px 0;
      background-color: $blue-faded;
      background-repeat: no-repeat;
      background-position:
        top 16px left 0,
        bottom 16px left 5px;
      background-size: 80px, 100px;
      border-radius: $border-radius-xl;

      @include media-breakpoint-up(md) {
        padding: 0 40px;
      }

      @include media-breakpoint-up(lg) {
        padding: 0;
      }

      @include media-breakpoint-up(xl) {
        background-position:
          top 30px left 0,
          bottom 31px left 5px;
        background-size: 122px, 129px;
      }

      @include media-breakpoint-down(lg) {
        background-image: none !important;
      }
    }

    &__content {
      margin: 0 auto 32px;
      text-align: center;

      @include media-breakpoint-up(md) {
        margin-bottom: 0;
        padding: 40px 0;
      }

      @include media-breakpoint-up(lg) {
        max-width: 380px;
      }

      @include media-breakpoint-up(xl) {
        max-width: 488px;
      }
    }

    &__img-content {
      width: 100%;
      padding: 8px 10px 0;
      overflow: hidden;

      @include media-breakpoint-up(md) {
        padding-top: 58px;
      }
    }

    &__img {
      position: relative;
      max-width: 344px;
      margin: 0 auto;
      margin-bottom: -8px;
      padding-bottom: 8px;
      overflow: hidden;
      background-color: $white;
      border-radius: $border-radius-lg $border-radius-lg 0 0;
      box-shadow: 10px -8px $blue;
    }
  }

  &-banner {
    &__block {
      position: relative;
      margin: 0 (-$container-padding);
      padding: 40px $container-padding 52px;
      overflow: hidden;

      @include media-breakpoint-up(sm) {
        margin: 0;
        border-radius: $border-radius-xl;
      }

      @include media-breakpoint-up(md) {
        padding: 0;
      }
    }

    &__bg {
      @include absolute-cover;

      background-repeat: no-repeat;
      background-position: bottom left;
      background-size: 165%;

      @include media-breakpoint-up(md) {
        background-size: 62%;
      }
    }

    &__content {
      width: 100%;
      max-width: 505px;
      margin: 0 auto;
      text-align: center;

      @include media-breakpoint-up(md) {
        padding: 32px 0 52px;
      }

      @include media-breakpoint-up(lg) {
        padding-top: 52px;
      }
    }

    &__img {
      width: 100%;
      max-width: 190px;
      margin: 0 auto;

      @include media-breakpoint-up(md) {
        max-width: 310px;
        margin: 0;
        padding: 52px 0 20px;
      }
    }

    &__dogs-tail {
      transform-origin: bottom center;
      animation: dogs-tail-animation 2s ease-in-out infinite;
    }
  }

  &-attention-grabbing {
    &__block {
      padding: 60px 0;
      background-color: $blue-faded;
      background-repeat: no-repeat;
      background-position: top 0 left 0;
      background-size: 100px;

      @include media-breakpoint-up(xxl) {
        background-size: 165px;
      }

      @include media-breakpoint-down(lg) {
        background-image: none !important;
      }

      &.bottom {
        background-position: bottom 0 left 0;
      }
    }
  }
}

.main {
  > .section {
    &:first-child {
      &:not(.section-welcome, .section-intro) {
        padding-top: $section-indent-mobile * 2;

        @include media-breakpoint-up(sm) {
          padding-top: 60px;
        }
      }
    }

    &:last-child {
      &:not(.section-welcome, .section-intro) {
        padding-bottom: $section-indent-mobile * 2;

        @include media-breakpoint-up(sm) {
          padding-bottom: $section-indent * 2;
        }
      }
    }

    &.section-contacts,
    &.section-attention-grabbing {
      &:last-child {
        padding-bottom: 0 !important;
      }
    }

    &.section-banner {
      &:last-child {
        @include media-breakpoint-down(sm) {
          padding-bottom: 0 !important;
        }
      }
    }
  }
}
