.link {
  &-white {
    transition: color 0.3s;

    &:hover {
      color: $primary !important;
    }
  }

  &-with-underline-hover {
    position: relative;
    display: inline-block;

    &:after {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 1px;
      background: currentColor;
      transform: scale3d(0, 1, 1);
      transform-origin: 100% 50%;
      transition: transform 0.3s;
      content: "";
      pointer-events: none;
    }

    &:hover {
      &:after {
        transform: scale3d(1, 1, 1);
        transform-origin: 0% 50%;
      }
    }
  }

  &-with-icon {
    position: relative;

    &.text-sm {
      padding-left: 32px;

      [class*="icomoon-"] {
        top: -2px;
        font-size: 24px;

        @include media-breakpoint-up(lg) {
          top: 1px;
        }
      }
    }

    [class*="icomoon-"] {
      position: absolute;
      left: 0;
      z-index: 5;
    }
  }
}
