.dropdown,
.dropdown-center {
  &.dropdown-header {
    .dropdown-toggle {
      position: relative;
      margin-bottom: 1rem;
      padding: 0;

      @include media-breakpoint-up(xl) {
        margin-bottom: 0;
        padding-right: 1.875rem;
      }

      @include media-breakpoint-down(xl) {
        display: block;
        width: 100%;
        color: $blue-light !important;
        font-weight: $font-weight-semibold !important;
        font-size: 1.375rem !important;
        cursor: text;
      }

      &:hover {
        @include media-breakpoint-up(xl) {
          color: $primary;
        }
      }

      &:after {
        position: absolute;
        top: 50%;
        right: 2px;
        z-index: 5;
        display: inline-flex;
        margin: 0;
        margin-top: 2px;
        font-weight: $font-weight-normal;
        font-family: "icomoon";
        line-height: 1;
        border: none;
        transform: translate(0, -50%);
        transition: transform 0.3s;
        content: "\e900";

        @include media-breakpoint-down(xl) {
          content: none;
        }
      }

      &[aria-expanded="true"] {
        &:after {
          transform: translate(0, -50%) rotate(180deg);
        }
      }
    }

    .dropdown-menu {
      max-width: 300px;
      padding: 0;
      color: $white;
      background-color: transparent;
      border: none;
      border-radius: 0 0 $border-radius-xl$border-radius-xl;

      @include media-breakpoint-up(xl) {
        padding: 30px 24px;
        background-color: $blue-dark;
      }

      @include media-breakpoint-down(xl) {
        position: static !important;
        display: block;
        transform: none !important;
      }

      > li {
        &:not(:last-child) {
          margin-bottom: 1.25rem;
        }
      }
    }

    .dropdown-item {
      display: inline-block;
      width: auto;
      padding: 0;
      color: $white;
      font-size: 1.25rem;
      background-color: transparent !important;
      transition: color 0.3s;

      @include media-breakpoint-up(xl) {
        font-size: 1.375rem;
      }

      &.with-icon {
        position: relative;
        padding-left: 2rem;

        [class*="icomoon-"] {
          position: absolute;
          top: 5%;
          left: 0;
          z-index: 5;
          font-size: 1.5rem;

          @include media-breakpoint-up(xl) {
            top: 10%;
          }
        }
      }

      &:hover {
        color: $primary;
        background-color: transparent;
      }
    }
  }
}
